<template>
  <div class="city">
      <div class="search_wrap">
          <div class="search">
              <i class="fa fa-search"></i>
              <input v-model="city_val" type="text" placeholder="请输入城市名">
          </div>
          <button @click="$router.push({name:'Address',params:{city:city}})">取消</button>
      </div>
      <div class="aa" v-if="searchList.length==0">
          <div class="location">
              <Location @click='selectCity({name:city})' :address='city'/>
          </div>
          <Alphabet @selectCity='selectCity' ref="allcity" :cityInfo='cityInfo' :keys='keys'/>
      </div>
      <div class="search_list" v-else>
          <ul>
              <li @click='selectCity(item)' v-for="(item,index) in searchList" :key="index">{{item.name}}</li>
          </ul>
      </div>
  </div>
</template>

<script>
import {
		ref,
		reactive,
		toRefs,
		onMounted,
		computed,
        watch,
        nextTick
    } from '@vue/composition-api'
import Location from '../components/Location'
import Alphabet from '../components/Alphabet'
import axios from 'axios'
export default {
    name:'City',
    components: {
        Location,
        Alphabet
    },
    setup(props,{root,refs}){
        const city_val =ref('')
        const cityInfo=ref(null);
        const keys=ref([]);
        const allCities =ref([]);
        const searchList=ref([]);
        const city=computed(()=>{
          return root.$store.getters.location.addressComponent.city || root.$store.getters.location.addressComponent.province
         })

        watch(()=>city_val.value,(value)=>{
            // console.log(city_val.value);
            searchCity()
        })

        const searchCity=()=>{
            if(!city_val.value){
                //如果搜索框为空,数组置空
                searchList.value=[]
            }else{
                //根据输入框的关键字检索 并存入到searchList数组中
                searchList.value=allCities.value.filter(item=>{
                    return item.name.indexOf(city_val.value) !=-1;
                })
                // console.log(searchList.value);
            }
        }

        const getCityInfo=()=>{
            axios.get('/api/posts/cities').then(res=>{
                cityInfo.value=res.data
                //处理key 计算key
                keys.value=Object.keys(res.data)
                // console.log(keys.value);
                //hotcities这个key移除掉
                keys.value.pop();
                // console.log(keys.value);
                //keys 排序
                keys.value.sort();
                // console.log(keys.value);
               nextTick(()=>{
                    refs.allcity.initScroll()
                })

                //存储所有城市,用来搜索过滤
                keys.value.forEach(key=>{
                    // console.log(key);
                    cityInfo.value[key].forEach(city=>{
                        // console.log(city);
                        allCities.value.push(city)
                    })
                })
            }).catch(err=>{
                console.log(err);
            })
        }

        const selectCity=(city)=>{
            // console.log(city);
            root.$router.push('/home')
            root.$router.push({name:'Address',params:{city:city.name}})
        }
        onMounted(() => {
            // console.log('aa');
            getCityInfo()
        })

        return{
            city_val,
            city,
            cityInfo,
            keys,
            selectCity,
            allCities,
            searchList
        }
    }
}
</script>

<style scoped>
.aa{
    height: 100%;
}
.city {
  width: 100%;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
  padding-top: 45px;
}
.search_wrap {
  position: fixed;
  top: 0;
  height: 45px;
  width: 100%;
  background: #fff;
  box-sizing: border-box;
  padding: 3px 16px;
  display: flex;
  justify-content: space-between;
}
.search {
  background-color: #eee;
  border-radius: 10px;
  line-height: 40px;
  width: 85%;
  box-sizing: border-box;
  padding: 0 16px;
}
.search input {
  background: #eee;
  outline: none;
  border: none;
  margin-left: 5px;
}
.search_wrap button {
  outline: none;
  color: #009eef;
}

.location {
  background: #fff;
  padding: 8px 16px;
  height: 65px;
  box-sizing: border-box;
}

.search_list {
  background: #fff;
  padding: 5px 16px;
}
.search_list li {
  padding: 10px;
  border-bottom: 1px solid #eee;
}
</style>
